import jwx from '../../common/weiPay/jws.js'
export default {
	data() {
		return {
			balance: 0,
			isSubmit: false, //是否提交中
			price: ''
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad(option) {},
	methods: {
		withdrawal(e) {
			//提现
			var _this = this;
			if (_this.isSubmit) return false;
			if (_this.price == '') {
				_this.$tools.toast('请输入正确的金额')
				return false;
			}
			_this.isSubmit = true;
			_this.$tools.loading('数据条件中')
			
			_this.$request.common
				.paySundries({
					type: 3,
					money: _this.price
				})
				.then(data => {
					_this.$tools.loadingHide();
					_this.isSubmit = false;
					if (data.status == 1) {
						_this.pay(data.msg);
					} else {
						_this.$refs.paymentPassword.modalFun('hide');
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$refs.paymentPassword.modalFun('hide');
					_this.isSubmit = false;
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		pay(orderInfo) {
			let _this = this;
			jwx({
				nonceStr: orderInfo.nonceStr,
				timeStamp: orderInfo.timeStamp,
				package: orderInfo.package,
				signType: orderInfo.signType,
				paySign: orderInfo.paySign
			}, res => {
				_this.$tools.toast('充值成功')
				setTimeout(() => {
					uni.navigateBack();
				}, 1000)
			}, fail => {
				console.log(fail)
				_this.$tools.toast('支付失败')
			})
		}
	}
};
